.contact{
margin: 80px auto;
max-width: 90%;
display: flex;
align-content:center ;
justify-content: space-between;
background-color: #999bc3;
}
.contact-col{
    flex-basis: 48%;
    color: #676767;
}
.contact-col h4{
    color: #000F38;
    font-weight: 500;
    font-size: 25px;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}
.contact-col h4 img{
    width: 40px;
    margin-left: 10px;
}
.contact-col p{
    max-width: 450px;
    list-style: 1.3;
}
.contact-col ul li{
    display: flex;
    align-items: center;
    margin: 20px 0;
}
.contact form input, .contact form textarea{
    display: block;
    width: 100%;
    background: #EBECFE;
    padding: 15px;
    border: 0;
    outline: 0;
    margin-bottom: 15px;
    margin-top: 5px;
    resize: none;


}
.contact-col span{
    display: block;
    margin: 20px 0;
    
}
/*----------------------*/
@media(max-width:700px){
    .contact{
        display: block;
    }
    .contact-col{
        padding: 20px;
    }
}
